@media (max-width: 375px) {
  .page__trockson_content img {
    max-width: 90vw;
  }
}

@media (max-width: 1279px) {
  .trockson__text_left::after {
    border-right: none !important;
  }
}

@media (max-width: 1023px) {
  .actu__content {
    padding: 1rem;
  }

  .actu__footer {
    padding: 0rem 1rem 1rem 1rem;
  }

  .actu__infos,
  .actu__footer-price {
    padding-bottom: 0.8rem;
  }
